body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

 a{
  margin: .5em;
  text-decoration: none;
  color: black;
}

#nav{
  padding: 0 5em;
}
.home{
  display: grid;
  grid-template-areas: "a"
  "b";
}

.top{
  grid-area: "a";
  display: -webkit-flex;
  display: flex;
  background-image: url("https://i.imgur.com/PepWXZB.jpg");
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 700px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.mid{
  grid-area: "b";
  background-color: whitesmoke;
  width: 100%;
  height: 500px;
}

.big{
  font-size: 4em;
}

.sub{
  font-size: 1.5em;
}

#bigger-button{
  padding: 1.25em 2.5em;
  font-size: 1em;
}
.prof {
  max-width: 382px;
  max-width: 400px;
}
#cardPic img{
  clip: rect(0px,30px,20px,0px);
}


.dashboard{
  display: grid;
  grid-template-areas: 
  "search"
  "main";
  margin-top: 5em;
  text-align: center;
}

.search{
  grid-area: "search";
  box-shadow: 2px 2px whitesmoke;
  border: solid 2px lightgrey;
  font-family: 'Heebo', sans-serif;
  width: 40%;
  height: 2em;
  margin: auto;
}

.main{
  grid-area: 'main';
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 85%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Heebo', sans-serif;
  margin: auto;
}
.App{
  font-family: 'Roboto';
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

a{
  text-decoration: none;
}
